<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  let sections = []
  let username
  let password
  let studentName
  let role
  let status
  let birthday
  let gender
  let address
  let contactNumber
  let section
  let guardianName
  let gurdianContactNumber

  onMount(async () => {
    sections = await fetch("/api/v1/section", {
      headers: getDefaultHeaders(),
    }).then((r) => r.json())
  })

  async function newStudents() {
    await fetch("/api/v1/student", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        userInfo: {
          user: {
            username,
            password,
            name: studentName,
            role,
            status,
          },
          birthday: new Date(birthday).toISOString(),
          gender,
          address,
          contactNumber,
        },
        section: {
          id: section.id,
        },
        guardian: {
          name: guardianName,
          contactNumber: gurdianContactNumber,
        },
      }),
    }).then(async () => await dispatch("refreshStudents"))
  }
</script>

<label>Username: <input type="text" bind:value={username} /></label>
<label>Password: <input type="password" bind:value={password} /></label>
<label>Name: <input type="text" bind:value={studentName} /></label>
<label>
  Section:
  <select bind:value={section}>
    <option value="">Select a Section</option>
    {#each sections as section}
      <option value={section}>{section.name}</option>
    {/each}
  </select>
</label>
<label>Status: <input type="text" bind:value={status} /></label>
<label>Birthday: <input type="datetime-local" bind:value={birthday} /></label>
<label>Gender: <input type="text" bind:value={gender} /></label>
<label>Address: <input type="text" bind:value={address} /></label>
<label>Contact Number: <input type="text" bind:value={contactNumber} /></label>
<label>Guardian Name: <input type="text" bind:value={guardianName} /></label>
<label
  >Guardian Contact number: <input
    type="text"
    bind:value={gurdianContactNumber}
  /></label
>
<button on:click={async () => await newStudents()}>Submit</button>
