<script>
  import { onMount, createEventDispatcher } from "svelte"
  import { getCurrentUserInfo, getDefaultHeaders } from "../utils.js"
  const dispatch = new createEventDispatcher()

  let name

  async function newSections() {
    await fetch("/api/v1/section", {
      headers: getDefaultHeaders(),
      method: "POST",
      body: JSON.stringify({
        name,
      }),
    }).then(async () => await dispatch("refreshSections"))
  }
</script>

<label>Name: <input type="text" bind:value={name} /></label>
<button on:click={async () => await newSections()}>Submit</button>
